//#region Global Imports
import * as React from 'react';
import { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Popover from '@material-ui/core/Popover';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
// import { UnderwritingContractsApi, UnderwritingBenefitsApi } from '@hims/api';
import { PlanTypeTabBar } from '../../Components/PlanTypeTabBar';
import { CategoryForm } from '../../Components/CategoryForm';
import { PackagesSidebarList } from '../../Components/PackagesSidebarList';
// import Container from '@material-ui/core/Container';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { BenefitDetailsForm } from '../../Components/BenefitDetailsForm';
import { AddPlanTypeModal } from '../../Components/Modal/AddPlanTypeModal';
import { LimitTypeItems } from '../../Components/BenefitsTable/DropdownConstants';
import { generatePlanNodes } from '../../CreateProposalPage';
//#endregion Global Imports

//#region Local Imports
//#endregion Local Imports

//#region Interface Imports
// import { ICreateProposal } from 'Pages/CreateProposal/ICreateProposal';
//#endregion Interface Imports

import Typography from '@material-ui/core/Typography';

import '@hims/core/dist/index.css';


const addLinkStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			maxWidth: 360,
			backgroundColor: theme.palette.background.paper,
		},
	}),
);

interface BenefitPlanProps {
	benefitNodes: any[];
	planTypeList: any[];
	contractNode: any;
	categoryNode: any;
	roomAndBoardList: any[];
	coverageList: any[];
	riderList: any[];
	rows: any[];
	selectedPlanType: string;
	selectedPlanPackage: string;
	selectedBenefitCategory: string;
	classes: any;
	submitMode?: boolean;
	SetBenefitNodes: any;
	SetSelectedPlanType: any;
	SetSelectedPlanPackage: any;
	SetSelectedBenefitCategory: any;
	SetNewNodeCounter: any;
	viewOnly?: boolean;
	newNodeCounter: number;
}

export const StandardBenefitPlan: React.FC<BenefitPlanProps> = (props: BenefitPlanProps): JSX.Element => {
	const { 
		benefitNodes,
		contractNode,
		categoryNode,
		rows,
		planTypeList, 
		roomAndBoardList, 
		coverageList, 
		riderList, 
		selectedPlanType,
		selectedPlanPackage,
		selectedBenefitCategory,
		SetBenefitNodes, 
		SetSelectedPlanType, 
		SetSelectedPlanPackage, 
		SetSelectedBenefitCategory,
		SetNewNodeCounter,
		classes,
		submitMode,
		viewOnly,
		newNodeCounter
	} = props;

	// const isInitialMount = useRef(true);
	// const [rootID] = useState('5da3fd4067fc03960173a18b');
	const [planTypeModal, setPlanTypeModal] = useState(false);
	// const [confirmationModal, setConfirmationModal] = useState(false);
	// const [confirmationSubmit, setConfirmationSubmit] = useState('');
	// const [confirmationMessage, setConfirmationMessage] = useState('');
	// const [successModal, setSuccessModal] = useState(false);
	// const [successMessage, setSuccessMessage] = useState('');
	const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
	const [popoverList, setPopoverList] = useState<any[]>([]);
	const popoverOpen = Boolean(anchorEl);

	const addLinkClasses = addLinkStyles();

	// const contractsApiInstance = new UnderwritingContractsApi();
	// const benefitsApiInstance = new UnderwritingBenefitsApi();

	
	const handleChangeBenefitForm = (e: any) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
		    item.level === 2 && 
		    item.code === 'CT'
		);

		let metadata = {};
		metadata[e.target.name] = e.target.value;

		if (nodes[index]['custom_metadata']) {
			nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);

			SetBenefitNodes(nodes);
		}
	}

	const handleChangeSubForm = (e: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		handleChangeSubForm2(e.target.name, e.target.value, field, fieldIndex);
	}

	const handleChangeSubForm2 = (name: string, value: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		let nodes = [...benefitNodes];

		let index = 0;

		index = nodes.findIndex(item => 
		  item.id === categoryNode.id &&
	      item.code === categoryNode.code &&
		  item.level === categoryNode.level && 
	      item.tree_path[2] === categoryNode.tree_path[2] && 
	      item.tree_path[3] === categoryNode.tree_path[3]
	    );

		let data: any = {};

		if (field === 'limits' && fieldIndex === -1) {
			data = value;
		} else {
			data[name] = value;
		}

		if (nodes[index][field]) {
			if (fieldIndex > -1) {
				nodes[index][field][fieldIndex] = Object.assign ({}, nodes[index][field][fieldIndex], data);
			} else {
				if (Array.isArray(data)) {
					nodes[index][field] = [...data];
				} else {
					nodes[index][field] = Object.assign ({}, nodes[index][field], data);
				}
			}

			SetBenefitNodes(nodes);
		}
	}

	const createNode = (count: number = 0, nodeData: any, template: any, benefitData: any, parentArray: object[], parentPos: number): [number, any[]] => {
		let newCount: number = count;
		let nodes: any[] = [...nodeData];

		parentArray.map((parent: any) => {
	    	const newID = "NODE-NEW-"+(newNodeCounter+newCount).toString().padStart(3, '0');
	    	let newBenefitData = Object.assign({}, benefitData);
			const childLevel: number = nodes[parent.index].level + 1;
	    	
			const newTreePath = [...newBenefitData['tree_path']];

	    	newTreePath[parentPos] = parent.code;

	    	newBenefitData['id'] = newID;
			newBenefitData['level'] = childLevel;
			newBenefitData['tree_path'] = newTreePath;

	    	if (childLevel === 5) {
	    		newBenefitData['type'] = 'BenefitCategory';
	    	}
	    	if (childLevel === 6) {
	    		newBenefitData['type'] = 'BenefitType';
	    	}
	    	if (childLevel === 7) {
	    		newBenefitData['type'] = 'Benefit';
	    	}
	    	if (childLevel === 8) {
	    		newBenefitData['type'] = 'Sub-Benefit';
	    	}

			const newNode = Object.assign({}, template, newBenefitData);

			let newChildren = [...nodes[parent.index].children];
			newChildren.push(newID);

			nodes[parent.index]['children'] = newChildren;
			
			nodes.push(newNode);
			newCount++;
		});

		return [newCount, nodes];
	}

	const handleCreateCivilStatus = (name: string = '', description: string = '') => {
		let nodes: any[] = [...benefitNodes];
		let parentArray: object[] = [];
		let templateNodes: object[] = [];
		let template: object | null = null;
		let count: number = 0;
		const level: number = 6;
		const childLevel: number = 7;

		parentArray = nodes.reduce((result, item, i) => {
	      if ( item.level === level &&
	        item.tree_path[2] === selectedPlanType  &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory 
	      ){
	      	const input = {
	      		index: i,
	      		code: item.code
	      	};

	      	result.push(input);
	      }
	      return result;
	    }, new Array());

	    templateNodes = nodes.reduce((result, item) => {
	      if ( item.level === childLevel &&
	        item.tree_path[2] === selectedPlanType  &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory
	      ){
	      	result.push(item);
	      }
	      return result;
	    }, new Array());

	    if (templateNodes.length > 0) {
	    	template = templateNodes[0];
	    }

	    if (template !== null) {
			const benefitData = {
			    name: name,
			    code: name.replace(/[^A-Z]/g, ''),
			    description: description,
			    metadata: {},
			    metadata_schema: {},
			    included: true,
			    children: [],
			    custom_metadata: Object.assign({title: name}),
			    custom_metadata_schema: {
			      properties: {
			        title: {type: 'string'},
			      },
			      required: [],
			    },
	    		tree_path: [
	    			"MCT",
	    			"CT",
	    			selectedPlanType,
	    			selectedPlanPackage,
	    			selectedBenefitCategory,
	    			"",
	    			name.replace(/[^A-Z]/g, '')
	    		],
			 };

			[count, nodes] = createNode(0, nodes, template, benefitData, parentArray, 5);

			SetBenefitNodes(nodes);
			SetNewNodeCounter(newNodeCounter + count);
		}
	}

	const handleCreateRelationship = (name: string = '', description: string = '', data: object = {}, parent: any | null = null) => {
		let nodes: any[] = [...benefitNodes];
		let templateNodes: object[] = [];
		let template: object | null = null;
		let count:number = 0;

		const level: number = parent.level;
		const childLevel: number = level + 1;

		const parentIndex = nodes.findIndex ((item) => {
			return 	item.level === level && 
					item.name === parent.name &&
					item.code === parent.code &&
			        item.tree_path[2] === parent.tree_path[2] &&
			        item.tree_path[3] === parent.tree_path[3] &&
			        item.tree_path[4] === parent.tree_path[4] &&
			        item.tree_path[5] === parent.tree_path[5] &&
			        item.tree_path[6] === parent.tree_path[6];
		});

		const parentArray = [{index: parentIndex, code: parent.code}];

	    templateNodes = nodes.reduce((result, item) => {
	      if ( item.level === childLevel &&
	        item.tree_path[2] === selectedPlanType  &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory
	      ){
	      	result.push(item);
	      }
	      return result;
	    }, new Array());

	    if (templateNodes.length > 0) {
	    	template = templateNodes[0];
	    }

	    if (template !== null) {
			const benefitData = {
			    name: name,
			    code: name.replace(/[^A-Z]/g, ''),
			    description: description,
			    metadata: {},
			    metadata_schema: {},
			    included: true,
			    children: [],
			    custom_metadata: Object.assign({title: name}, data),
			    custom_metadata_schema: {
			      properties: {
			        title: {type: 'string'},
			      },
			      required: [],
			    },
	    		tree_path: [
	    			"MCT",
	    			"CT",
	    			selectedPlanType,
	    			selectedPlanPackage,
	    			selectedBenefitCategory,
	    			'DP',
	    			'',
	    			name.replace(/\s+/g, '').toUpperCase()
	    		],
			 };

			[count, nodes] = createNode(0, nodes, template, benefitData, parentArray, 6);

			SetBenefitNodes(nodes);
			SetNewNodeCounter(newNodeCounter + count);
		}
	}

	const createExclusionNode = (count: number, nodeData: any[], template: any, parent: any): [number, any[]] => {
		let nodes = [...nodeData];
		let parentPos = 3;

		const parentIndex = nodes.findIndex ((item) => {
			return 	item.level === parent.level && 
					item.name === parent.name &&
					item.code === parent.code &&
			        item.tree_path[2] === parent.tree_path[2] &&
			        item.tree_path[3] === parent.tree_path[3];
		});

		const parentArray = [{index: parentIndex, code: parent.code}];

		let benefitData = {
		    name: template.name,
		    code: template.code,
		    description: template.description,
		    metadata: {},
		    metadata_schema: {},
		    included: true,
		    children: [],
		    custom_metadata: Object.assign({title: template.name}),
		    custom_metadata_schema: {
		      properties: {
		        title: {type: 'string'},
		      },
		      required: [],
		    },
		    renderClause: "",
			renderContract: "",
			renderProposal: "",
		    sort_index: template.sort_index,
		    original_node: template.id
		 };

		if (parent.level === 4) {
			benefitData['tree_path'] = [
    			"MCT",
    			"CT",
    			selectedPlanType,
    			"",
    			template.code
    		];
    		parentPos = 3;
		}

		if (parent.level === 5) {
			benefitData['tree_path'] = [
    			"MCT",
    			"CT",
    			selectedPlanType,
    			'EX',
    			"",
    			template.code
    		];
    		parentPos = 4;
		}
		
		[count, nodes] = createNode(count, nodes, template, benefitData, parentArray, parentPos);

		return [count, nodes];
	}


	const handleCreateExclusion = (nodeData: any[], template: object, PNIndex: number = -1) => {
		let nodes = [...nodeData];
		let count: number = 0;

		let newTemplate = Object.assign({}, template);

		if (selectedBenefitCategory === 'NT' && PNIndex > -1) {
			const partner = newTemplate['custom_metadata']['partner_network_list'][PNIndex];

			newTemplate['name'] = partner.name;
			newTemplate['code'] = partner.facility_id;
			newTemplate['description'] = partner.type;
		} 

		let parentCategory = nodes.find ((item: any) => {
			return item.level === 5 &&
					item.name === categoryNode.name &&
					item.code === categoryNode.code &&
			        item.tree_path[2] === selectedPlanType &&
			        item.tree_path[3] === 'EX' &&
			        item.tree_path[4] === categoryNode.code;
		});

		const exclusionNode = nodes.find ((item: any) => {
			return item.level === 4 &&
					item.code === 'EX' &&
			        item.tree_path[2] === selectedPlanType &&
			        item.tree_path[3] === 'EX' 
		});


		if (!parentCategory) {
			[count, nodes] = createExclusionNode(count, nodes, categoryNode, exclusionNode);

			parentCategory = nodes.find ((item: any) => {
			return item.level === 5 &&
						item.name === categoryNode.name &&
						item.code === categoryNode.code &&
				        item.tree_path[2] === selectedPlanType &&
				        item.tree_path[3] === 'EX' &&
				        item.tree_path[4] === categoryNode.code;
			});
		}

		[count, nodes] = createExclusionNode(count, nodes, newTemplate, parentCategory);

		SetNewNodeCounter(newNodeCounter + count);
		

		return nodes;
	}

	const handleDeleteExclusion = (nodeData: any[], template: any, PNIndex: number = -1) => {
		let nodes = [...nodeData];
		let newTemplate = Object.assign({}, template);
		let parentCode = categoryNode.code;

		if (selectedBenefitCategory === 'NT' && PNIndex > -1) {
			const partner = newTemplate['custom_metadata']['partner_network_list'][PNIndex];

			newTemplate['name'] = partner.name;
			newTemplate['code'] = partner.facility_id;
		}

		if (selectedBenefitCategory === 'EX') {
			parentCode = template.tree_path[4];
		}

		const parentIndex = nodes.findIndex ((item: any) => {
			return item.level === 5 &&
					item.code === parentCode &&
			        item.tree_path[2] === selectedPlanType &&
			        item.tree_path[3] === 'EX' &&
			        item.tree_path[4] === parentCode;
		});

		const exclusionIndex = nodes.findIndex ((item: any) => {
			return item.level === 6 &&
					item.code === newTemplate.code &&
			        item.tree_path[2] === selectedPlanType &&
			        item.tree_path[3] === 'EX' &&
			        item.tree_path[4] === parentCode &&
			        item.tree_path[5] === newTemplate.code
		});

		// get exclusion category and the matching node, remove from parent category

		if (parentIndex > -1 && exclusionIndex > -1 && !['SX','EF','TP'].includes(parentCode)) {
			const exclusionID = nodes[exclusionIndex].id;
			const originalID = nodes[exclusionIndex].original_node;

			let newChildren = [...nodes[parentIndex].children];
			newChildren = newChildren.filter((child) => child !== exclusionID);

			nodes[parentIndex]['children'] = newChildren;

			const originalIndex = nodes.findIndex ((item) => item.id === originalID);

			nodes[originalIndex]['included'] = true;

			nodes = nodes.filter((item) => item.id !== exclusionID);
		}
		
		return nodes;
	}


	const handleChangeRowMetadata = (e: any, id: number) => {
		handleChangeRowMetadata2(e.target.name, e.target.value, id);
	}

	const handleChangeRowMetadata2 = (name: string, value: any, id: number, metaIndex: number = -1, metaName: string = '') => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.id === id
		);

		let metadata = {};	

		if (metaIndex > -1 && metaName) {
			metadata = Object.assign({}, nodes[index]['custom_metadata']);
			if (metadata[name]) {
				metadata[name][metaIndex][metaName] = value;

				if (selectedBenefitCategory === 'NT' && name === 'partner_network_list' && metaName === 'included') {
					if (value === false) {
						nodes = handleCreateExclusion(nodes, nodes[index], metaIndex);
					} else {
						nodes = handleDeleteExclusion(nodes, nodes[index], metaIndex);
					}
				}
			}
		} else {
			metadata[name] = value;
		}

		if (nodes[index]['custom_metadata']) {
			nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);

			if (nodes[index].tree_path[2] !== 'BASE') {
				nodes[index]['edited'] = true;
			}

			SetBenefitNodes(nodes);
		}
	}

	const handleChangeRowLimits = (e: any, id: number, i: number) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.id === id
		);

		const limValue = e.target.value;
		const limName = e.target.name;
		
		let limits = [...nodes[index]['limits']];

		if (nodes[index]['limits']) {
			if (limName === 'limit_type') {
				const newLimits = limValue.reduce((result, val) => {
					const limType = LimitTypeItems.find((limType) => limType.label === val);

					if (limType) {
						const found = limits.find((item) => item.type === limType.type && item.limit_unit === limType.limit_unit);

						if (found) {
							result.push(found);
						} else {
							const input = {
								type: limType.type,
								limit: 0,
								limit_unit: limType.limit_unit,
								daily_limit: 0,
							};

							result.push(input);	
						}
					} 

					return result;
				}, new Array());

				limits = newLimits;
			} else if (['limit','daily_limit'].includes(limName)) {
				limits[i][limName] = parseFloat(limValue);
			}

			nodes[index]['limits'] = limits;

			SetBenefitNodes(nodes);
		}
	}


	const handleChangeRowSelection = (rowObjs: any[]) => {
		let nodes = [...benefitNodes];

		rowObjs.map ((rowObj) => {
			const index = nodes.findIndex(item => 
				item.id === rowObj.id
			);
			if (nodes[index]['included'] !== rowObj.included) {
				nodes[index]['included'] = rowObj.included;

				if (selectedBenefitCategory === 'EX') {
					if (!rowObj.included) {
						nodes = handleDeleteExclusion(nodes, nodes[index]);
					} 
				} else if (selectedBenefitCategory !== 'EL') {
					if (rowObj.included === false) {
						nodes = handleCreateExclusion(nodes, nodes[index]);
					} else {
						nodes = handleDeleteExclusion(nodes, nodes[index]);
					}
				}
			}	
		});

		SetBenefitNodes(nodes);
	}

	const handlePackagePopoverOpen = (
		e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
		planPackage: string,
	) => {
		const currentTarget = e.currentTarget;

		let nodes: any[] = [];
	
		if (planPackage === 'CV') {
			nodes = coverageList.filter((item: any) => {
				return !item.included;
			});
		}

		if (planPackage === 'RI') {
			nodes = riderList.filter((item: any) => {
				return !item.included;
			});
		}

		setPopoverList(nodes);
		setAnchorEl(currentTarget);
	}
	const handlePlanModalOpen = () => {
		setPlanTypeModal(true);
	}

	const handlePlanModalClose = () => {
		setPlanTypeModal(false);
	}

	const handlePopoverClose = () => {
		setAnchorEl(null);
	}

	const handlePopoverClick = (planType: string, planPackage: string, benefitCategory: string) => {
		addBenefitCategory(planType, planPackage, benefitCategory);
	}

	const deleteBenefitCategory = (category: any) => {
		const nodePath = category.tree_path;

		const nodes = benefitNodes.map((item: any) => {
			
			if (
				item.tree_path[1] === undefined ||
				item.tree_path[2] === undefined ||
				item.tree_path[3] === undefined ||
				item.tree_path[4] === undefined
			) {
				return item;
			}

			if (
				item.level <= 6 &&
				item.tree_path[1] === nodePath[1] &&
				item.tree_path[2] === nodePath[2] &&
				item.tree_path[3] === nodePath[3] &&
				item.tree_path[4] === nodePath[4]
			) {
				let newItem = Object.assign({}, item);
				newItem['included'] = false;
				return newItem;
			};

			return item;
		});

		SetBenefitNodes(nodes);
	}

	const addBenefitCategory = (planType: string, planPackage: string, benefitCategory: string) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.level === 5 && 
			item.code === benefitCategory &&
			item.tree_path[2] === planType && 
			item.tree_path[3] === planPackage && 
			item.tree_path[4] === benefitCategory
		);

		nodes[index]['included'] = true;
		SetBenefitNodes(nodes);
		setAnchorEl(null);
	}

	// const deletePlanType() {}

	const addPlanType = (data: any) => {
		let nodes = [...benefitNodes];
		
		const {planNodes, planTypeID} = generatePlanNodes(nodes, data['name'], data['code']);

		const index = nodes.findIndex(item => 
			item.level === 2 && 
			item.code === 'CT'
		);

		nodes[index]['children'].push(planTypeID);

		SetBenefitNodes([...nodes, ...planNodes]);
	}

	return (
		<div>
			<Grid container className={classes.gridContainer}>
			
				<Grid item sm={12}>
					{	!submitMode && !viewOnly ?
							<React.Fragment>
								<Typography gutterBottom
									style={{ marginRight: '20px' }}
									className={classes.sectionTitle}
								>
									Standard Benefit Plan
								</Typography>
								<Typography display="inline" className={classes.subSectionTitle} gutterBottom>
									You are currently customizing this benefit plan
								</Typography>
							</React.Fragment> : null
					}
					{
						submitMode ? 	
							<Typography gutterBottom
								className={classes.sectionTitle}
							>
								Customized Proposal
							</Typography> : null
					}
					{
						viewOnly ? 
							<Typography gutterBottom
								className={classes.sectionTitle}
							>
								Review Benefits
							</Typography> : null
					}
				</Grid> 

				<BenefitDetailsForm 
					classes={classes}
					contractNode={contractNode}
					setChangeBenefitForm={handleChangeBenefitForm}
					viewOnly={viewOnly ? viewOnly : false}
				/>

				<Grid item sm={12}>
					<PlanTypeTabBar
						selectedPlanType={selectedPlanType}
						planTypeList={planTypeList}
						setSelectedPlanType={SetSelectedPlanType}
						setModalOpen={handlePlanModalOpen}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Grid item sm={12}>
					<Divider variant="middle" />
				</Grid>

				<Grid item sm={2}>
					<PackagesSidebarList
						selectedBenefitCategory={selectedBenefitCategory}
						roomAndBoardList={roomAndBoardList}
						riderList={riderList}
						coverageList={coverageList}
						setSelectedPlanPackage={SetSelectedPlanPackage}
						setSelectedBenefitCategory={SetSelectedBenefitCategory}
						deleteBenefitCategory={deleteBenefitCategory}
						setPopoverOpen={handlePackagePopoverOpen}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Grid item sm={10}>
					<CategoryForm
						selectedPlanType={selectedPlanType}
						selectedBenefitCategory={selectedBenefitCategory}
						setRowSelection={!viewOnly ? handleChangeRowSelection : () => {}}
						setRowLimits={!viewOnly ? handleChangeRowLimits : () => {}}
						setRowMetadata={!viewOnly ? handleChangeRowMetadata : () => {}}
						setRowMetadata2={!viewOnly ? handleChangeRowMetadata2 : () => {}}
						setSubForm={!viewOnly ? handleChangeSubForm : () => {}}
						setSubForm2={!viewOnly ? handleChangeSubForm2 : () => {}}
						createCivilStatus={!viewOnly? handleCreateCivilStatus : () => {}}
						createRelationship={!viewOnly? handleCreateRelationship : () => {}}
						subFormNode={categoryNode}
						rows={rows}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Popover
					open={popoverOpen}
					anchorEl={anchorEl}
					onClose={handlePopoverClose}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'left',
					}}
					transformOrigin={{
						vertical: 'top',
						horizontal: 'left',
					}}
				>
					<List classes={addLinkClasses}>
						{popoverList.length > 0 ? (
							popoverList.map((item: any) =>
								item['name'] && item['code'] ? (
									<ListItem
										button
										key={`${item['code']}`}
										onClick={() =>
											handlePopoverClick(
												item['tree_path'][2] !== undefined
													? item['tree_path'][2]
													: null,
												item['tree_path'][3] !== undefined
													? item['tree_path'][3]
													: null,
												item['tree_path'][4] !== undefined
													? item['tree_path'][4]
													: null,
											)
										}
									>
										<ListItemText
											disableTypography={true}
											primary={`${item['name']}`}
										/>
									</ListItem>
								) : null,
							)
						) : (
							<ListItem>
								<ListItemText disableTypography={true} primary={'Cannot add more.'} />
							</ListItem>
						)}
					</List>
				</Popover>
			</Grid>

			<AddPlanTypeModal onClose={handlePlanModalClose} isModalOpen={planTypeModal} onSubmit={addPlanType} />
		</div>
	);
};
